import React from 'react'
import App, { Container } from 'next/app'
import FullContainer from "../components/FullContainer.js"

export default class MyApp extends App {
  static async getInitialProps({ Component, router, ctx }) {
    let pageProps = {}
    console.log('getting props from app')
    if (Component.getInitialProps) {

      pageProps = await Component.getInitialProps(ctx)
    }

    return { pageProps }
  }

  render () {
    const { Component, pageProps } = this.props

    console.log('iRRR app render', this.props)

    // component corresponds to specific component to render
    // <FullContainer>
    //   </FullContainer>
    return (
      <Container>
        <FullContainer domeState={pageProps.domeState}>
        </FullContainer>
        <Component {...pageProps} />

      </Container>
    )
  }
}
